/* 스크롤 구현 위해서는 이 부분 제거하고
pinchAble 변화에 따라 touch-area 스타일 조정 */
#root > div {
    touch-action: none;
}

.touch-area {
    height: 90vh;
    /* touch-action: pinch-zoom; */
    /* background-color: rgb(203, 240, 255); */
}

.cards {
    /* height: 90%; */
    display: grid;
    /* grid-template-columns: repeat(auto-fill, minmax(30%, 1fr)); */
    grid-gap: 5px;
    max-width: 90%;
    margin: 0 auto 30px;
}

.card-area {
    position: relative;
}

.card-img {
    height: 100%;
    width: 100%;
    object-fit: cover;
}
